import config from "../config";

export const ldaEndspoint = {
  sendLDAData,
  getDataBase,
  pin,
  uploadDatabase,
  sendSeedOrInteractiveData
};

async function sendLDAData(
  iterations,
  mode,
  alpha,
  beta,
  database,
  topics,
  file,
  steeming
) {
  console.log(file);

  let form = new FormData();
  form.append("token", config.password);
  form.append("iterations", iterations);
  form.append("mode", mode);
  form.append("alpha", alpha);
  form.append("database", database);
  form.append("beta", beta);
  form.append("topics", topics);
  form.append("stopwords", file);
  form.append("steeming", steeming ? "True" : "False");

  const requestOptions = {
    method: "POST",
    body: form
  };
  return fetch(`${config.backend_page}/lda`, requestOptions).then(response =>
    response.text()
  );
}

async function pin() {
  const requestOptions = {
    method: "POST"
  };
  return fetch(`${config.backend_page}/pin`, requestOptions).then(response =>
    response.text()
  );
}
async function getDataBase() {
  const requestOptions = {
    method: "POST"
  };
  return fetch(`${config.backend_page}/database`, requestOptions)
    .then(response => response.text())
    .catch(e => {
      return JSON.stringify([
        ["NewGroups.5", "New Groups (5 categorías)"],
        ["NewGroups.10", "New Groups (10 categorías)"]
      ]);
    });
}
async function uploadDatabase(file, fileName) {
  let form = new FormData();
  form.append("token", config.password);
  form.append("file", file);
  form.append("name", fileName);

  const requestOptions = {
    method: "POST",
    body: form
  };
  return fetch(`${config.backend_page}/uploadFile`, requestOptions).then(
    response => response.text()
  );
}

function sendSeedOrInteractiveData(
  iterations,
  mode,
  alpha,
  beta,
  database,
  topics,
  nu,
  seed,
  file,
  steeming
) {
  console.log(file);
  let form = new FormData();
  form.append("token", config.password);
  form.append("iterations", iterations);
  form.append("mode", mode);
  form.append("alpha", alpha);
  form.append("database", database);
  form.append("beta", beta);
  form.append("topics", topics);
  form.append("nu", nu);
  form.append("seeds", JSON.stringify(seed));
  form.append("stopwords", file);
  form.append("steeming", steeming ? "True" : "False");
  const requestOptions = {
    method: "POST",
    body: form
  };
  return fetch(`${config.backend_page}/lda`, requestOptions).then(response =>
    response.text()
  );
}
